import {Injectable} from '@angular/core';
import {WebsocketService} from './websocket-service';
import {StoreService} from '../store-service';
import {HttpService} from './http-service';
import {Observable} from 'rxjs/Observable';
import 'rxjs/add/operator/merge';
import 'rxjs/add/operator/map';

export interface Parameter {
  command: { path: string };
  parameters: object;
}

export interface WsResponse {
  code: number;
  command: { path: string };
  data: object | string;
  msg: string;
  detail?: string | object;
  isError?: boolean
}

export interface HttpResponse {
  code: number;
  command: string | {path: string};
  msg: string;
  data: string | object
}

/**
 * @description This service is used to generate final parameters for server.
 * */
@Injectable()
export class ExchangeService {
  constructor(private wsService: WebsocketService,
              private httpService: HttpService,
              private storeService: StoreService) {
    this.handleMessage();
    this.wsService.connectionStatus.subscribe(status => console.log(status));
  }

  handleWebsocketRequest(parameter: Parameter){
    this.wsService.send(parameter);
  }

  handleHttpRequest(parameter: any) {

  }

  getAllResponse(): Observable<WsResponse | HttpResponse> {
    const wsResponse$: Observable<WsResponse> = this.wsService.messages.map((msg: WsResponse) => {
      msg.isError = msg.code > 2000;
      return msg;
    });
    const httpResponse$: Observable<HttpResponse> = this.httpService.response$;

    return wsResponse$.merge(httpResponse$);
  }

  login(parameters: Parameter) {
    // const command = {path: this.command.login};
    this.wsService.send(parameters);
  }

  handleMessage(){
    this.wsService.messages.map((message: WsResponse) => {
      message.isError = message.code > 2000;
      return message;
    }).subscribe(this.storeService.subject);
  }

}



